<template>
  <div class="warper">
    <div id="container">
      <el-amap vid="amap" :zoom="zoom" :center="center" class="amap-demo">
        <el-amap-marker v-for="(marker, index) in markers" :key="index" :position="marker.position"
                        :icon="'https://webapi.amap.com/theme/v1.3/markers/n/loc.png'" :events="marker.events"
                        :visible="marker.visible" :draggable="marker.draggable" :vid="index"></el-amap-marker>
        <el-amap-info-window
          :size="300"
          :visible="contentWindow.visible"
          :position="contentWindow.position"
          :content="contentWindow.content"
          :events="contentWindow.events"
          :offset="[0.5,-20]"
        >
        </el-amap-info-window>
      </el-amap>
    </div>
    <div class="operate operate-left">
      <div id="up_location" @click.stop="refeshLocation">
        <figure>
          <img src="../assets/image/home/message-refesh.png" alt="">
        </figure>
        <p>刷新定位</p>
      </div>
      <div>
        <figure>
          <img src="../assets/image/home/navigation.png" alt="">
        </figure>
        <p>一键导航</p>
      </div>
      <div>
        <figure @click.stop="sheetVisible=true">
          <img src="../assets/image/home/track.png" alt="">
        </figure>
        <p>定位设置</p>
      </div>
    </div>
    <div class="operate operate-right">
      <div>
        <figure class="stop" @click.stop="directionClk">
          <img :src="boxshow?'../../static/image/Open.png':'../../static/image/stop.png'" id="tip" alt="">
        </figure>
        <p class="text-center" id="direction">{{!boxshow?'展开':'收起'}}</p>
        <transition name="mybox">
          <div class="op-list" :style="{height:hgauto+'px'}">
            <div>
              <router-link tag="figure" to="/enclosure">
                <img src="../assets/image/home/pen.png" alt="">
              </router-link>
              <p>电子围栏</p>
            </div>
            <div>
              <router-link tag="figure" to="/track">
                <img src="../assets/image/home/trail.png" alt="">
              </router-link>
              <p>轨迹回放</p>
            </div>
            <div>
              <figure @click.stop="callphone">
                <img src="../assets/image/home/call.png" alt="">
              </figure>
              <p>呼叫设备</p>
            </div>
          </div>
        </transition>
      </div>
    </div>
    <mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
  </div>
</template>
<script>
import { lazyAMapApiLoaderInstance } from "vue-amap";
import { mapGetters } from "vuex";

const exampleComponents = {
  props: ["text"],
  template: `<div>text from  parent: {{text}}</div>`
};
let geolocation, infoWindow;
export default {
  data() {
    return {
      list: "",
      boxshow: true,
      hgauto: 300,
      sheetVisible: false,
      actions: [
        { name: "追踪模式(实时)", method: this.chooseClk, id: 0 },
        { name: "运动模式(1分钟)", method: this.chooseClk, id: 1 },
        { name: "省电模式(10分钟)", method: this.chooseClk, id: 2 }
      ],
      address: "",
      uid: "862035034036254", //IMEI设备号
      deviceInfo: {
        ele: "",
        time: ""
      },
      count: 1,
      slotStyle: {
        padding: "2px 8px",
        background: "#eee",
        color: "#333",
        border: "1px solid #aaa"
      },
      zoom: 14,
      center: [121.5273285, 31.21515044],
      markers: [
        {
          position: [121.5273285, 31.21515044],
          events: {
            click: () => {
              // alert("click marker");
            },
            dragend: e => {
              this.markers[0].position = [e.lnglat.lng, e.lnglat.lat];
            }
          },
          visible: true,
          draggable: false,
          template: "<span>1</span>"
        }
      ],
      componentMarker: {
        position: [121.5273285, 31.21315058]
      },
      slotMarker: {
        position: [121.5073285, 31.21715058]
      },
      contentWindow: {
        position: [121.5273285, 31.21515044],
        content: `<div class="windowInfo"><p>电量：%</p><p>位置：</p><p>状态：暂无</p><p>更新时间：</p></div>`,
        visible: true
      }
    };
  },
  computed: {
    ...mapGetters(["userInfo"])
  },
  created() {
    if (this.$route.query.userToken) {
      this.$store.commit("setuser", this.$route.query);
    }
    //调用app方法
    var ua = navigator.userAgent.toLowerCase();//获取当前设备信息
    if (/iphone|ipad|ipod/.test(ua)) {//如果是ios
      try{
        window.toggleNav('home');
      }catch (e){
        if (e instanceof TypeError){
          console.log('无该函数方法')
        }
      }
    } else if (/android/.test(ua)) {//安卓
      try{
        window.clientJS.toggleNav('home');
      }catch (e){
        if (e instanceof TypeError){
          console.log('无该函数方法')
        }
      }

    }
  },
  // sockets: {
  //   connect(val) {
  //     console.log("连接成功");
  //     this.id = this.$socket.id;
  //     this.$socket.emit("login", this.userInfo.uid); //在这里触发connect事件
  //   },
  //   customEmit(val) {
  //     console.log(
  //       'this method was fired by the socket server. eg: io.emit("customEmit", data)'
  //     );
  //   },
  //   msg(val) {
  //     //接收服务端的推送的消息
  //     this.deviceInfo = JSON.parse(val);
  //     this.setPosition();
  //   }
  // },
  mounted() {
    let _this = this;
  },
  methods: {
    setPosition() {
      this.center = [this.deviceInfo.lng, this.deviceInfo.lat];
      this.markers[0].position = [this.deviceInfo.lng, this.deviceInfo.lat];
      this.contentWindow.position = [this.deviceInfo.lng, this.deviceInfo.lat];
      this.contentWindow.content = `<div class="windowInfo"><p>电量：${
        this.deviceInfo.ele == "" ? "暂无" : this.deviceInfo.ele + "%"
      }</p><p>位置：${this.showCityInfo(
        this.deviceInfo.lng,
        this.deviceInfo.lat
      )}</p><p>状态：暂无</p><p>更新时间：${this.getLocalTime(
        this.deviceInfo.time
      )}</p></div>`;
    },
    showCityInfo(lng, lat) {
      //通过经度纬度逆地理编码
      let _this = this;
      lazyAMapApiLoaderInstance.load().then(() => {
        var geocoder = new AMap.Geocoder({
          radius: 1000,
          extensions: "all"
        });
        geocoder.getAddress([lng, lat], function(status, result) {
          if (status === "complete" && result.info === "OK") {
            if (result && result.regeocode) {
              _this.address = result.regeocode.formattedAddress;
              _this.$nextTick();
            }
          }
        });
      });
      return this.address;
    },
    refeshLocation() {
      //刷新定位
      this.Indicator.open({
        text: "正在刷新定位...",
        spinnerType: "fading-circle"
      });
      setTimeout(res => {
        this.contentWindow.visible = false;
        this.$nextTick(() => {
          this.contentWindow.visible = true;
        });
        this.Indicator.close();
      },1500);
    },
    directionClk() {
      //展开收起操作
      this.boxshow = this.boxshow ? false : true;
    },
    callphone() {
      //呼叫设备
      window.location.href = `tel:${this.userInfo.telephone}`;
    },
    chooseClk(val) {
      //更改定位模式
      this.$http
        .post(`${this.HOST}post/editLocationModel`, {
          userToken: this.userInfo.userToken,
          imei: this.userInfo.uid,
          model: val.id
        })
        .then(res => {
          if (res.data.code == "000000") {
            this.toast("操作成功");
          } else {
            this.toast(res.data.msg);
          }
        })
        .catch(err => {
          this.toast("操作失败");
        });
    }
  },
  watch: {
    boxshow() {
      this.hgauto = this.boxshow ? 300 : 0;
    }
  }
};
</script>
<style lang="scss">
@import "../assets/sass/_mixins.scss";
@import "../assets/sass/_parameters.scss";

.warper {
  height: 100%;
}

#container {
  height: 100%;
  margin: 0px;
  position: relative;
}

.operate {
  display: flex;
  flex-direction: column;
  position: fixed;
  z-index: 2000;
  width: 60px;
  font-size: 14px;
  .span {
    display: inline-block;
  }
  &.operate-left {
    bottom: 30px;
    left: 10px;
    right: 0;
    div {
      margin-bottom: 10px;
      color: $map-btn-color;
      figure {
        margin: 0;
        margin: auto;
        background-color: $map-btn-color;
        @include wh(45px, 45px);
        border-radius: 50%;
        padding: 5px;
        box-sizing: border-box;
        img {
          max-width: 100%;
        }
      }
    }
  }
  &.operate-right {
    top: 30px;
    right: 10px;
    div {
      margin-bottom: 10px;
      color: $map-btn-color;
      figure {
        margin: 0;
        margin: auto;
        background-color: $map-btn-color;
        @include wh(45px, 45px);
        border-radius: 50%;
        padding: 5px;
        box-sizing: border-box;
        img {
          max-width: 100%;
        }
      }
    }
    .op-list {
      margin-top: 12px;
      transition: height 0.2s;
      overflow: hidden;
    }
  }
}

.btn-circle {
  @include wh(45px, 45px);
  border-radius: 50%;
  background-color: $map-btn-color;
  padding: 8px;
  box-sizing: border-box;
}

.windowInfo {
  width: 180px;
  p {
    margin: 0;
  }
}
</style>




